<template>
  <div id="home">
    <v-container style="margin-top: 25px; height: 50%;">
            <v-system-bar color="primary"></v-system-bar>
            <v-card-text class="my-4 text-center title">
              <span style="text-shadow: 1px 1px 1px black; margin-right: 5px">
                RECURSO EDUCATIVO AVAENGLISH
              </span>
            </v-card-text>
          <v-btn v-if="btnActive"
          :href=urlResource
          target="_blank"
      :loading="loading3"
      :disabled="loading3"
      color="red"
      class="ma-2 white--text"
      @click="loader = 'loading3'"
    >
      IR AL RECURSO
      <v-icon
        right
        dark
      >
        mdi-gamepad
      </v-icon>
    </v-btn>
     <v-card-text v-else class="text-center title">
              <span style="color:black">
AVAENGLISH DISPONIBLE TODOS LOS JUEVES Y DOMINGO DE 5PM A 7PM.             
              </span>
              
            </v-card-text>
          <br>
          <br>
     <iframe
           id="iframe"
              :src="urlResource"
              frameborder="0"
            ></iframe>
       
    </v-container>
<br v-for="(j) in 9" :key="j">
        <Footer></Footer>
  </div>
</template>
<script>
import Footer from "@/components/Default/Footer"
export default {
  components: {
    Footer,
  },
  name: "Resource",
  data() {
    return {
  hoy:new Date(),
  btnActive:false,
urlResource: "https://avarpg.000webhostapp.com/AVAENGLISH/index.html",
    };
  },
  mounted() {
    if (this.$route) this.route = this.$route.query.redirect;
var time = 10000;
    if (this.hourAval()==true) {
this.btnActive=true

    }
    setInterval(function () {
        if (this.hourAval()==true) {
this.btnActive=true
        }
    }, time);
  },
  methods: {
hourAval() {
    var day = '' + this.hoy.getDay()
    var hora = '' + this.hoy.getHours()
    if (day==0) {
        return true;
    }
    if (day==4) {
        if (hora==17||hora==18) {
        return true;
        }
    }
    return false;
}

  },
};
</script>

<style scoped>
.header-login {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}
.header-login h3 {
  color: #607d8b;
  text-transform: uppercase;
  font-weight: 600;
  margin-bottom: 1rem;
}
/* DESKTOP */

@media all and (max-width: 999999999px) and (min-width: 1340px) {
  /* Footer Parent Div Styling */
#iframe {
  width:80%;
  height:80%
 }
#loginbtn {
    display: none;
  }
  #footer {
    width: 100%;
    background-color: #fff;
    margin-top: 30px !important;
  }
}
/* MOBILE */

@media all and (max-width: 599px) and (min-width: 100px) {
  /* Footer Parent Div Styling */
  #loginbtn {
    display: block;
  }
  #loginform {
    display: none;
  }
}
 
</style>
